<template>
  <div>
    <NavBar :title='$t("common.navTitle")'></NavBar>

    <div class="form-wrap login-form">
          <div class="login-form-item">
            <cube-input v-model="model.telephone" :placeholder='$t("common.telephoneNumberPlaceholder")' :maxlength="20" v-if="model.telephoneLogin">
              <svg class="icon" aria-hidden="true" slot="prepend">
                <use xlink:href="#icon-shoujihao"></use>
              </svg>
            </cube-input>
            <cube-input v-model="model.userId" :placeholder='$t("common.idPlaceholder")' :maxlength="30" v-else>
              <svg class="icon" aria-hidden="true" slot="prepend">
                <use xlink:href="#icon-character"></use>
              </svg>
            </cube-input>
          </div>
          <div class="login-form-item">
            <cube-input v-model="model.userPwd" type="password" :placeholder='$t("common.pwdPlaceholder")' :eye="eye" :maxlength="20">
              <svg class="icon" aria-hidden="true" slot="prepend">
                <use xlink:href="#icon-mima"></use>
              </svg>
            </cube-input>
          </div>

          <!-- <div>
           <a href="javascript:;" class="link login-type" @click="toggleLoginType">{{this.model.telephoneLogin ? $t("common.loginByID"): $t("common.loginByTelephoneNumber")}}</a>
          </div> -->
          <router-link to="register" class="link go-register">{{$t("common.applyRegister")}}</router-link>
        <div class="submit-wrap">
          <UButton buttonType="submit" size="large" @click="submitHandler">{{$t("common.login")}}</UButton>
        </div>
    </div>


    <LangTool />
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import UButton from '@/components/U-Button.vue';
import LangTool from '@/components/LangTool.vue';
import {
  /* eslint-disable no-unused-vars */
  Style,
  DatePicker,
} from 'cube-ui';
import { trimData } from '../utils/utils';

const apis = {
  login: '/h5/login',
};

export default {
  data() {
    return {
      validity: {},
      valid: undefined,
      model: {
        userId: '',
        userPwd: '',
        telephone: '',
        telephoneLogin: false,
      },
      eye: {
        open: false,
        reverse: false,
      },
    };
  },
  methods: {
    checkData(data) {
      const {
        userId = '', userPwd = '', telephone = '', telephoneLogin,
      } = data;
      const rules = [
        [(!userId) && (!telephoneLogin), this.$t('common.idRequired')],
        [(!telephone) && telephoneLogin, this.$t('common.telephoneNumberPlaceholder')],
        [(!(/^([()+-\s0-9]){6,20}$/.test(telephone))) && telephoneLogin, this.$t('common.telephoneError')],
        [!userPwd, this.$t('common.pwdRequired')],
      ];
      return rules.every((r) => {
        if (r[0]) {
          this.showToastType(r[1]);
          return false;
        }
        return true;
      });
    },
    submitHandler() {
      const fixedData = trimData(this.model);
      const {
        userId = '', userPwd = '', telephone = '', telephoneLogin,
      } = fixedData;
      if (this.checkData(fixedData)) {
        console.log('valid');
        const data = {
          passwd: userPwd,
          people_id: telephoneLogin ? '' : userId,
          telephone: telephoneLogin ? telephone : '',
          telephoneLogin,
        };
        this.http.post(apis.login, data).then((res) => {
          if (res.code === 1000) {
            const { people_name: peopleName } = res.data;
            this.$storage.setItem('user', {
              peopleName,
              telephone: res.data.telephone,
            }, false);
            this.$router.push('index');
          } else {
            const err = telephoneLogin ? this.$t('common.loginError2') : this.$t('common.loginError1');
            this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : err);
          }
        });
      }
    },
    showToastType(txt, type = 'error') {
      const toast = this.$createToast({
        txt,
        type,
      });
      toast.show();
    },
    toggleLoginType() {
      this.model.telephoneLogin = !this.model.telephoneLogin;
    },
  },
  components: {
    NavBar,
    UButton,
    LangTool,
  },
};
</script>

<style lang="stylus" scoped>
  @import "../assets/styles/common.styl";
  @import "../assets/styles/login-form.styl";
  .form-wrap {
    margin: 0 auto;
    padding-top: 19px;
    width: 300px;
    .go-register {
      float: right;
      margin-top: 59px;
      font-size:$size-normal;
      h(20px);
    }
    .login-type {
      float: left;
    }
    .submit-wrap {
      margin-top: 120px;
      padding-bottom: 60px;
    }
  }

</style>
