      <a href="javascript:;" @click="toggleLang('zh-CN')" class="link">{{$t('common.zhCN')}}</a> <span class="split">|</span>
      <a href="javascript:;" @click="toggleLang('zh-TW')" class="link">{{$t('common.zhTW')}}</a> <span class="split">|</span>
      <a href="javascript:;" @click="toggleLang('en-US')" class="link">{{$t('common.en')}}</a>
    </div> -->
    <div></div>
</template>

<script>
import CubeUI from 'cube-ui';
import enUSMessages from 'cube-ui/src/locale/lang/en-US';
import zhCNMessages from 'cube-ui/src/locale/lang/zh-CN';
// 自己引入繁体中文语言包
import zhTWMessages from '../locales/cube-ui/zh_TW';

const Locale = CubeUI.Locale;

export default {
  methods: {
    toggleLang(lang) {
      localStorage.setItem('lang', lang);
      switch (lang) {
        case 'en-US':
          Locale.use('en-US', enUSMessages);
          break;
        case 'zh-TW':
          Locale.use('zh-TW', zhTWMessages);
          break;
        default:
          Locale.use('zh-CN', zhCNMessages);
      }
      this.$i18n.locale = lang;
    },
  },
};
</script>

